@use "sass:math";

body {
  @include flexbox();
  @include flex-direction(column);
  @include align-items(center);
  @include justify-content(center);
  min-height: 100vh;
  padding: $gutter;
  background: smokewhite;
}
* {
  box-sizing: border-box;
}
.row {
  @include flexbox();
  @include flex-direction(column);
  clear: both;
  width: 100%;
  @media (min-width: $screen-md) {
    @include flex-direction(row);
  }
}
.container {
  width: 100%;
  max-width: $screen-xl;
  margin: 0 auto;
}
div[class^="col-"] {
  padding: $gutter * 2;
}

// https://www.gavsblog.com/blog/for-each-while-loops-sass-scss
$columns: 16;

// https://sass-lang.com/d/slash-div
@media (min-width: $screen-md) {
  @for $i from 1 through $columns {
    .col-#{$i} {
        width: math.div(100, $columns) * $i * 1%;
    }
  }
}

$marginbuffers: 5;
@for $i from 0 through $marginbuffers {
  .my-#{$i} {
    margin-top: $i * ($theme-font-size-base * .5);
    margin-bottom: $i * ($theme-font-size-base * .5);
  }
  .mx-#{$i} {
    margin-right: $i * ($theme-font-size-base * .5);
    margin-left: $i * ($theme-font-size-base * .5);
  }
  .mt-#{$i} {
    margin-top: $i * ($theme-font-size-base * .5);
  }
  .mb-#{$i} {
    margin-bottom: $i * ($theme-font-size-base * .5);
  }
  .ml-#{$i} {
    margin-left: $i * ($theme-font-size-base * .5);
  }
  .mr-#{$i} {
    margin-right: $i * ($theme-font-size-base * .5);
  }
}
